.footer-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.footer-contact {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 35px;
  line-height: 34px;
  color: #CFCFCF;

  width: 299px;
  height: 62px;
  background: #413F3F;
  border-radius: 25px;
  margin-bottom: 25px;
}

.footer-social {
  display: flex;
  padding: 0px 20%;
  justify-content: center;
  margin-bottom: 25px;
  width: 100%;
  flex-wrap: wrap;
}

a .footer-icon {
  width: 95px;
  height: 95px;
  background: #C4C4C4;
  border-radius: 50%;
  margin: 15px;
  cursor: pointer;
}

.footer-message {
  display: flex;
  justify-content: space-between;
  width: 100%;
  color: white;
  padding: 10px 25px;
}

.rights {
  text-align: right;
}