.faq-section {
  display: flex;
  flex-direction: column;
}

.faq-row {
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
}

.module {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0px 30px 60px;
  width: 452px;
}

.faq-title {
  background: #EBEBEB;
  border-radius: 25px;
  font-size: 35px;
  line-height: 34px;
  text-align: center;
  color: #000000;
  width: 478px;
  padding: 20px 30px;
  cursor: pointer;
}

.faq-answer {
  background: rgba(73, 73, 73, 0.2);
  font-size: 25px;
  line-height: 24px;
  text-align: center;
  color: #413F3F;
  padding: 40px;
  width: 435px;
  transition: visibility 0.5s, opacity 0.5s linear;
}